<template>
  <div class="home">
    <div class="topBgcolor"></div>

    <!-- 头部导航 -->
    <div class="nav-bar">
      <template class="nav-left fl">
        <div class="home-title one-text">{{timeText}}<span v-if="userInfo.nickName">，{{userInfo.nickName}}</span></div>
      </template>

      <!-- 搜索图标 -->
      <template class="nav-content fl">
        <van-icon
          class="pro-icon"
          name="search"
          size="20"
          @click="btnShow()"
        />
      </template>

      <!-- 扫码 -->
      <template class="nav-content1 fl">
        <van-icon
          class="pro-sao"
          name="scan"
          size="20"
        />
      </template>
      <!-- 日历 -->
      <template class="nav-right fr">
        <van-icon
          class="data-icon"
          size="20"
          name="calender-o"
          @click="show = true"
        />
      </template>

      <van-calendar
        v-model="show"
        color="#0c34ba"
        @confirm="onConfirm"
      />

    </div>

    <!-- 搜索弹出页面 -->
    <div class="search-prop">
      <div class="topBgcolor-1"></div>
      <!-- 搜索框 -->
      <van-search
        class="tab-btn"
        v-model="value"
        show-action
        v-show="btnshow"
        shape="round"
        placeholder="请输入搜索关键词"
        @input="onSearch"
        @cancel="onCancel"
      />

      <!-- 搜索弹出层 -->
      <van-popup
        v-model="btnshow"
        position="top"
      >
        <!-- 搜索发现 -->
        <div
          class="search-box"
          v-if="searchList.length>0"
        >
          <div>
            <h3 class="product-h3">搜索发现：</h3>
            <div class="product-box clearfix">
              <div
                class="product-item fl"
                :class="[index % 2 == 0 ? 'p-left' : 'p-right']"
                v-for="(item, index) in searchList"
                :key="index"
                @click="viewProductInfo(item.pid)"
              >
                <div class="p-img">
                  <img
                    class="auto-img"
                    :src="item.smallImg"
                    alt=""
                  >
                </div>
                <div class="pro-name one-text">{{item.name}}</div>
                <div class="clearFix">
                  <!-- <div class="en-pro-name onr-text fl">heitangbobonaicha</div> -->
                  <div class="pro-price fl">{{item.price}}</div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div
          v-show="hasSearch"
          class="null-box"
        >
          <van-empty
            image="search"
            description="查无此商品，请重新输入"
          />
        </div>

        <!-- 猜你喜欢 -->
        <div class="like-box">
          <h3 class="like-h3">猜你喜欢：</h3>
          <div
            class="product-list"
            v-for="(item,index) in hotProducts"
            :key="index"
          >
            <div class="pro-img">
              <img
                class="auto-img"
                :src="item.smallImg"
                alt=""
              >
            </div>
            <div class="pro-info">
              <div class="pro-text">
                <div class="ch-name">{{item.name}}</div>
                <!-- <div class="en-name">item.enname}}</div> -->
                <div class="pro-price">￥{{item.price}}</div>
              </div>
            </div>
            <div class="pro-add">
              <van-icon
                name="hot-o"
                size="30"
                color="#0b34ba"
              />
            </div>
          </div>

        </div>

      </van-popup>
    </div>

    <!-- 轮播图 -->
    <div class="banner-box">
      <van-swipe
        class="my-swipe"
        :autoplay="3000 "
        indicator-color="#0b34ba"
      >
        <van-swipe-item
          v-for="(item,index) in bannerData"
          :key='index'
        >
          <img
            class="auto-img"
            :src="item.bannerImg"
            alt=""
            @click="viewProductInfo(item.pid)"
          >
          <div class="banner-text">{{item.name}}</div>
        </van-swipe-item>
      </van-swipe>
    </div>

    <!-- 商品推荐 -->
    <div class="hot-product">
      <div class="hot-product-title">热门推荐：</div>
      <div class="product-box clearfix">
        <div
          class="product-item fl"
          :class="[index % 2 == 0 ? 'p-left' : 'p-right']"
          v-for="(item, index) in hotProducts"
          :key="index"
          @click="viewProductInfo(item.pid)"
        >
          <div class="p-img">
            <img
              class="auto-img"
              :src="item.smallImg"
              alt=""
            >
          </div>
          <div class="pro-name one-text">{{item.name}}</div>
          <div class="clearFix">
            <!-- <div class="en-pro-name onr-text fl">heitangbobonaicha</div> -->
            <div class="pro-price fl">{{item.price}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- 主题色  #0b34ba -->
</template>

<script>
import "../../assets/less/home.css";
import { Toast } from "vant";

export default {
  // 需要缓存的组件的名称
  name: "Home",
  data() {
    return {
      // 头部时间段说明
      timeText: "",
      // 轮播图
      bannerData: [],
      // 热门推荐
      hotProducts: [],
      // 判断日历显示/隐藏
      show: false,
      date: "",
      userInfo: {},
      // 搜索框输入的值
      value: "",
      // 判断搜索按钮弹出层是否显示
      btnshow: false,
      // 热门商品列表
      hotProducts: [],
      // 查到的商品数据
      searchList: [],
      // 判断查询结果
      hasSearch: false,
    };
  },

  created() {
    this.getTime();
    this.getbannerData();
    this.getHotProducts();
    this.getUserInfo();
  },

  methods: {
    // 获取时间段
    getTime() {
      let hours = new Date().getHours();
      if (hours >= 6 && hours < 12) {
        this.timeText = "早上好";
      } else if (hours >= 12 && hours < 18) {
        this.timeText = "下午好";
      } else if (hours >= 18 && hours < 24) {
        this.timeText = "晚上好";
      } else {
        this.timeText = "深夜好";
      }
    },

    // 点击显示搜索框
    btnShow() {
      this.btnshow = true;
    },

    // 搜索框变化
    onSearch() {
      // 发起请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/search",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          name: this.value,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "Q001") {
            if (this.value == "") {
              this.hasSearch = false;
              this.searchList = [];
              return;
            }
            if (result.data.result.length == 0) {
              this.hasSearch = true;
              // return
            }
            this.searchList = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 查看商品详情
    viewProductInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },

    // 搜索框取消
    onCancel() {
      Toast("取消");
      this.btnshow = false;
      // this.popshow = false
    },

    // 获取猜你喜欢商品推荐数据
    getHotProducts() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 500) {
            this.hotProducts = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          //
        });
    },

    // 日历隐藏
    onConfirm() {
      this.show = false;
    },

    // 获取banner图
    getbannerData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/banner",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 300) {
            this.bannerData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          //
        });
    },

    // 获取热门商品推荐数据
    getHotProducts() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.code == 500) {
            this.hotProducts = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          //
        });
    },

    // 查看商品详情
    viewProductInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },

    // 获取用户信息
    getUserInfo() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          //

          if (result.data.code == "A001") {
            if (result.data.result.length > 0) {
              this.isLogin = true;
              this.userInfo = result.data.result[0];
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>
